import {
  Component,
  ContentChild,
  Input,
  OnChanges,
  TemplateRef
} from '@angular/core';
import { Changes } from 'ngx-reactivetoolkit';
import { Observable } from 'rxjs';
import { Step } from './types/step.type';

@Component({
  selector: 'sof-vertical-steps',
  template: `
    <ul>
      <li
        *ngFor="let item of internalSteps; let i = index"
        [class.active]="
          (i <= activeIndex && !reverse) || (i >= activeIndex && reverse)
        "
        [class.last-active]="i === activeIndex"
      >
        <span class="bullet bullet-{{ sizeBullets }}">
          <sof-svg-icon
            icon="icon-check"
            [size]="sizeBullets"
            *ngIf="
              (i <= activeIndex && !reverse) || (i >= activeIndex && reverse)
            "
          ></sof-svg-icon>
        </span>
        <div class="content content-{{ sizeBullets }}">
          <ng-container
            *ngIf="headerTemplateRef"
            [ngTemplateOutlet]="headerTemplateRef"
            [ngTemplateOutletContext]="{ $implicit: item }"
          >
          </ng-container>
        </div>
      </li>
    </ul>
  `,
  styleUrls: ['./vertical-steps.component.scss']
})
export class VerticalStepsComponent implements OnChanges {
  internalSteps: Step<any>[];
  activeIndex: number;

  /**
   * The size of the bullets.
   * Can be 8 - 12 - 16.
   * 12 by default
   */
  @Input() sizeBullets: '8' | '12' | '16' = '12';

  /**
   * Input to reverse the direction of the steppers.
   * Meaning that the stepper will start at the bottom, and not at the top (default).
   */
  @Input() reverse = false;

  @Input() set steps(items: Step<any>[]) {
    this.activeIndex = items.indexOf([...items].reverse().find(x => x.active));
    this.internalSteps = items.map((step, index) => ({
      ...step,
      active: index === this.activeIndex
    }));
  }

  @Changes('steps') steps$: Observable<Step<any>[]>;
  @ContentChild('step', { static: false }) headerTemplateRef: TemplateRef<any>;

  ngOnChanges(): void {}
}
